.container {
  perspective: 1000px;
}

.project {
  display: flex;
  justify-content: space-between;
  height: 20rem;
  width: 100rem;
  background-color: rgb(233, 233, 233);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  margin: 2em auto;
  border-radius: 12px;
  transform: rotateX(5deg);
  animation: card-not-active 0.5s linear;

  @media (max-width: 1082px) {
    width: 92.4%;
  }

  &__info {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__name {
    margin: 0.5em;
    font-size: 3.2rem;
    font-weight: bold;

    @media (max-width: 800px) {
      font-size: 1.6em;
    }
  }

  &__link {
    color: black;
    text-decoration: none;

    &:hover {
      color: rgb(117, 117, 117);
    }
  }

  &__description,
  &__stack {
    margin: 1em;
  }

  &__stack-list {
    display: inline;
    font-style: italic;
  }

  &--flip {
    animation: card-flip 1.5s linear 1;
  }

  &:hover {
    animation: card-active 0.5s linear;
    transform: rotateX(0);
  }

  &__image-container {
    display: flex;
    align-items: center;
    padding: 1em;
    max-width: 30%;

    @media (max-width: 800px) {
      max-width: 40%;
    }

    @media (max-width: 570px) {
      max-width: 50%;
    }
  }

  &__image {
    max-width: 100%;
    border-radius: 0.5em;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);

    &:hover {
      transform: scale(1.005);
      box-shadow: 0 2px 8px rgba(0, 0, 0.1, 0.25);
    }
  }
}

@media (max-width: 1082px) {
  .card {
    width: 92.4%;
    background-color: rgb(233, 233, 233);

    &--small {
      display: flex;
      flex-direction: column;
      height: fit-content;
      margin: 1.5em auto;
      padding: 0.3em;
      border-radius: 6px;
      box-shadow: 0 1px 4px rgba(0, 0, 0, 0.25);
      transform: none;
      animation: none;

      &:hover {
        animation: none;
      }
    }

    &--small-top {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
    }

    &--small-close {
      display: none;
    }

    &--small-open {
      display: flex;
      flex-direction: column;
    }

    &--small-mid {
      display: flex;

      @media (max-width: 570px) {
        flex-direction: column;
      }
    }
  }
}

.card-arrow-div {
  border: 1px solid rgba(128, 128, 128, 0.433);
  margin-right: 35px;
  border-radius: 50%;
  padding: 4px;
  cursor: pointer;
}

@keyframes card-flip {
  0% {
    transform: rotateX(90deg);
    opacity: 0;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0);
  }

  30% {
    transform: rotateX(-20deg);
    opacity: 1;
  }

  40% {
    transform: rotateX(20deg);
  }

  50% {
    transform: rotateX(-10deg);
  }

  60% {
    transform: rotateX(10deg);
  }

  70% {
    transform: rotateX(-5deg);
  }

  80% {
    transform: rotateX(5deg);
  }

  90% {
    transform: rotateX(0);
  }

  100% {
    transform: rotateX(5deg);
  }
}

@keyframes card-active {
  0% {
    transform: rotateX(5deg);
  }

  100% {
    transform: rotateX(0);
  }
}

@keyframes card-not-active {
  0% {
    transform: rotateX(0);
  }

  100% {
    transform: rotateX(5deg);
  }
}
