.modal {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 10;
  background-color: rgba(0, 0, 0, 0.7);
}

.nav {
  position: absolute;
  right: 0;
  background-color: rgb(255, 255, 255, 0.95);
  display: flex;
  flex-direction: column;
  width: fit-content;

  &__item {
    margin: 0;
    list-style: none;
  }

  &__button {
    border: 0;
    background-color: transparent;
    cursor: pointer;
    width: 300px;
    height: 100px;
    margin: 0;
    font-size: 22px;
    font-weight: bold;
  }
}

.nav__item + .nav__item {
  border-top: 1px solid rgb(175, 175, 175);
}
