.bio {
  width: 92.4%;
  margin: auto;

  &__container {
    @media (min-width: 1082px) {
      // display: flex;
    }
  }

  &__left {
    @media (min-width: 800px) {
      display: flex;
      flex-direction: column;
      align-items: center;
      // width: 50%;
    }
  }

  &__right {
    @media (min-width: 1082px) {
      width: 100rem;
      margin: auto;
    }
  }

  &__headshot {
    display: flex;
    justify-content: center;
  }

  &__img {
    width: 200px;
  }

  &__sub-heading {
    margin: 2rem 0;
  }

  &__text {
    margin: 1.5rem 0;
  }

  &__link {
    text-decoration: none;
  }

  &__codewars {
    color: black;
    display: flex;
    flex-direction: column;
    width: fit-content;
    max-width: 100%;
    margin: 5rem auto;
    border: 4px solid grey;
    background-color: grey;
    border-radius: 4px;
    padding: 1rem;
    cursor: pointer;
  }

  &__codewars-label {
    font-size: 2rem;
    font-weight: 700;
    margin-bottom: 4px;
    cursor: pointer;
  }

  &__codewars-img {
    max-width: 100%;
  }
}
