.container {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  min-height: 250px;
  max-height: 400px;
  height: 40vh;

  @media (max-width: 800px) {
    width: 100%;
  }

  @media (max-width: 576px) {
    max-height: none;
    height: fit-content;
  }
}

.icons {
  @media (max-width: 576px) {
    display: flex;
    flex-direction: column;
  }

  &__img {
    margin: 0 2em;
    animation: contact-icons 1s linear;
    width: 128px;

    @media (max-width: 576px) {
      margin: 1em 0;
      animation: contact-icons 1s linear;
      width: 100px;
    }
  }

  &__img:hover {
    transform: scale(1.1);
  }
}

@keyframes contact-icons {
  0% {
    transform: translateY(-100px);
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
