@use "../../partials/mixins" as *;

.header {
  position: sticky;
  top: 0;
  left: 0;
  z-index: 10;
  background-color: rgb(255, 255, 255, 0.95);

  &__container {
    display: flex;
    justify-content: space-between;
    max-width: 1400px;
    margin: 0 auto;
  }

  &__logo-container {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px 38px;
  }

  &__logo {
    color: black;
    text-decoration: none;
    font-size: 3.4rem;
    font-weight: bold;
  }

  &__icon-container {
    display: flex;
    align-items: center;
    padding: 0 50px;
    cursor: pointer;
  }

  &__icon {
    width: 4rem;
  }
}

.nav {
  display: flex;
  width: fit-content;
  justify-content: center;
  align-items: center;

  &__item {
    text-align: center;
    width: 10rem;
    list-style: none;
    margin: 0 2em;
  }

  &__link {
    font-size: 2.2rem;
    font-weight: bold;
    color: black;
    text-decoration: none;

    &--active {
      color: grey;
    }

    &:hover {
      color: grey;
    }
  }
}
