*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
  min-width: fit-content;
  font-family: Arial, Helvetica, sans-serif;
}

.counter {
  display: none;
}
