@use "../partials/variables" as *;
@use "../partials/mixins" as *;

.main {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  min-height: 250px;
  max-height: 400px;
  height: 40vh;
}

.message {
  text-align: center;
  animation: dropdown $dropdown-length cubic-bezier(0, 0, 0.35, 2);
  min-width: fit-content;

  @include media-mobile-tablet {
    width: 100vw;
    margin: 0 auto;
  }

  &__first {
    font-size: 3.4rem;
    font-weight: bold;

    @include media-mobile-tablet {
      font-size: 2.72rem;
    }
  }

  &__second {
    font-size: 2.2rem;
    font-weight: bold;

    @include media-mobile-tablet {
      font-size: 1.76rem;
    }
  }

  &__stacks {
    display: flex;
    justify-content: space-evenly;
    margin: 0 auto;
    margin-top: 50px;

    @include media-mobile-tablet {
      width: 280px;
      flex-wrap: wrap;
    }
  }

  &__stack {
    font-size: 1.8rem;
    font-weight: bold;
    margin: 0 0.4rem;
    animation: stack-fly-in 0.5s ease-out;
    animation-fill-mode: backwards;
    animation-delay: $dropdown-length;
    list-style: none;
    $i: 0;

    @for $num from 2 through 10 {
      &:nth-child(#{$num}) {
        $i: $i + 0.2s;
        animation-delay: ($dropdown-length + $i);
      }
    }

    @include media-mobile-tablet {
      font-size: 1.5rem;
      margin-bottom: 0.5rem;
    }
  }
}

/*KEYFRAMES*/
@keyframes dropdown {
  0% {
    transform: translateY(-400px);
  }

  80% {
    transform: translateY(0);
  }

  90% {
    transform: translateY(-15px);
  }

  100% {
    transform: translateY(0);
  }
}

@keyframes stack-fly-in {
  0% {
    transform: translateX(100%);
    opacity: 0;
  }

  100% {
    transform: translateX(0);
  }
}
